import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { DataPegawaiRoutingModule } from './data-pegawai-routing.module';
import { DetailComponent } from './detail/detail.component';
import { EditComponent } from './edit/edit.component';
import { NgZorroAntdModule } from 'ng-zorro-antd';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';


@NgModule({
  declarations: [DetailComponent, EditComponent],
  imports: [
    CommonModule,
    DataPegawaiRoutingModule,
    NgZorroAntdModule,
    ReactiveFormsModule,
    FormsModule,
    PerfectScrollbarModule,
],
providers:
    [
        FormBuilderTypeSafe
    ],
})
export class DataPegawaiModule { }
