import { Jadwal_tipe } from 'src/app/sdk/hr/models/jadwal-_tipe';
import { DirektoriPegawaiStatusService } from 'src/app/sdk/hr/services/direktori-pegawai-status.service';
import { Component, OnInit } from '@angular/core';
import { DirektoriPegawaiService, GaKlaimKategoriService, PayrollPtkpService, AbsensiJadwalTipeService, DirektoriPegawaiTypeService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';
import { NzMessageService } from 'ng-zorro-antd';
import { Pegawai, Klaim_kategori, Ptkp, Pegawai_status, Pegawai_type } from 'src/app/sdk/hr/models';
import { Status } from 'src/app/sdk/asset/models';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private direktoriPegawaiService: DirektoriPegawaiService,
        private gaKlaimKategoriService: GaKlaimKategoriService,
        private direktoriPegawaiStatusService: DirektoriPegawaiStatusService,
        private payrollPtkpService: PayrollPtkpService,
        private absensiJadwalTipeService: AbsensiJadwalTipeService,
        private direktoriPegawaiTypeService: DirektoriPegawaiTypeService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private pegawaiDetailService: PegawaiDetailService,
        private nzMessageService: NzMessageService
    ) { }

    pegawai: Pegawai = {}
    claim: Klaim_kategori = {}
    ptkp: Ptkp = {}
    status: Pegawai_status = {}
    jadwal: Jadwal_tipe = {}
    type: Pegawai_type = {}

    id_pegawai_biodata: string = ''
    id_pegawai: string = ''

    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.getPegawai()
    }

    getPegawai() {
        this.direktoriPegawaiService.getDirektoriPegawaisId(this.id_pegawai).subscribe(data => {
            this.pegawai = data.data;
            this.getKlaim(data.data.id_klaim_kategori)
            this.getPtkp(data.data.id_ptkp)
            this.getStatus(data.data.status_pegawai)
            this.getType(data.data.type_pegawai)
            this.getJadwal(data.data.id_jadwal_tipe)
         })
    }

    getKlaim(id_klaim_kategori) {
        this.gaKlaimKategoriService.getGaKlaimKategorisId(id_klaim_kategori).subscribe(
            data=> {
                this.claim = data.data
            }
        )
    }

    getPtkp(id_ptkp) {
        this.payrollPtkpService.getPayrollPtkpsId(id_ptkp).subscribe(
            data=> {
                this.ptkp = data.data
            }
        )
    }

    getType(type_pegawai) {
        this.direktoriPegawaiTypeService.getDirektoriPegawaiTypesId(type_pegawai).subscribe(
            data=> {
                this.type = data.data
            }
        )
    }
    getJadwal(id_jadwal_tipe) {
        this.absensiJadwalTipeService.getAbsensiJadwalTipesId(id_jadwal_tipe).subscribe(
            data=> {
                this.jadwal = data.data
            }
        )
    }
    getStatus(status_pegawai) {
        this.direktoriPegawaiStatusService.getDirektoriPegawaiStatusesId(status_pegawai).subscribe(
            data=> {
                this.status = data.data
            }
        )
    }

}
